@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "./components/burger";

h1 {
    font-family: 'Rubik';
     font-style: normal;
     font-weight: 900;
     font-size: 31.635px;
     line-height: 47px;   
     letter-spacing: 1px;
     color: #40A3F8;
}
.section {
    padding-top: 110px;
}
h2 {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 70px;
    color: #3E3E3E;
    text-align: center;
}
p {
    width: 300px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    color: rgba(0, 0, 0, 0.5);
}
.section:nth-child(1) {
    .container__block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 50px;
        .block {
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon {
                width: 80px;
                height: 80px;
                position: relative;
                border-radius: 50%;
                img {
                    position: absolute;
                    left: 20%;
                    top: 25%;
                }
            }
            .icon:nth-child(1) {
                background: linear-gradient(180deg, rgba(0, 158, 255, 0.1) 0%, rgba(56, 175, 255, 0.1) 100%);
                mix-blend-mode: multiply;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            }
            .icon:nth-child(2) {
                background: radial-gradient(51.35% 53.44% at 48.82% 46.56%, rgba(255, 170, 6, 0.12) 0%, rgba(255, 180, 26, 0.12) 100%);    
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            }
            .icon:nth-child(3) {
    
            }
            h3 {
                padding-top: 5px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 32px;
                color: #3D3D3F;
                padding-bottom: 20px;
            }
            p {
                width: 300px;
                text-align: center;
                font-size: 12px;
                line-height: 20px;
            }
        }
        .block:nth-child(2) {
            .icon {
                background: radial-gradient(51.35% 53.44% at 48.82% 46.56%, rgba(255, 170, 6, 0.12) 0%, rgba(255, 180, 26, 0.12) 100%);    
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                img {
                    left: 24%;
                    top: 22%;
                }
            }
        }
    }
}
.section:nth-child(2) {
    .block__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .img {
            width: 545px;
        }
        .block__img {
            width: 545px;
            position: relative;
            img:nth-child(1) {
                position: absolute;
                top: -212px;
                z-index: 5;
            }
            img:nth-child(2) {
                position: absolute;
                top: 80px;
                z-index: 1;
            }
            img:nth-child(3) {
                position: absolute;
                top: -90px;
                z-index: 7;
            }
            .block__doctor {
                position: relative;
                width: 270px;
                height: 174px;
                z-index: 5;
                left: -100px;
                top: -50px;
                background: #FEFEFE;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.04);
                border-radius: 20px;

                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .block__img {
                    display: flex;
                    align-items: center;
                    width: 270px;
                    img {
                        position: static;
                        padding-right: 10px;
                        height: 90px;
                    }
                    .block__header {
                        display: flex;
                        flex-direction: column;
                        align-items: start;
                        h5 {

                        }
                        .header__text {
                            display: flex;
                            align-items: center;
                            span {
                                font-family: 'Poppins';
                                font-style: normal;
                                font-weight: 400;
                                font-size: 12px;
                                line-height: 18px;
                                color: rgba(61, 61, 63, 0.4);
                            }
                            .star {
                                padding-left: 5px;
                                display: flex;
                                align-items: center;
                                img {
                                    padding-right: 2px;
                                    height: 10px;
                                }
                                span {
                                    font-family: 'Poppins';
                                    font-style: normal;
                                    font-weight: 400;
                                    font-size: 12px;
                                    line-height: 18px;
                                    color: #3D3D3F;
                                }
                            }
                        }
                    }
                }
                .block__main {
                    display: flex;
                    align-items: center;
                    width: 210px;
                    justify-content: center;
                    .text {
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        padding: 0px 10px;
                        p {
                            width: auto;
                        }
                        .p__text {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 22px;
                            display: flex;
                            align-items: center;
                            letter-spacing: 0.02em;
                            color: #A0A2B3;
                        }
                        .span__text {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 12px;
                            display: flex;
                            align-items: center;
                            color: #636481;
                        }
                    }
                }
                .block__footer {
                    padding-top: 14px;
                    display: flex;
                    align-items: center;
                    img {
                        padding-right: 10px;
                        position: static;
                    }
                    span {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 12px;
                        line-height: 18px;
                        letter-spacing: 0.02em;
                        color: #3D3D3F;
                    }
                }
            }
            .block__woman {
                z-index: 10;
                top: -260px;
                left: 340px;
                position: relative;
                width: 230px;
                height: 80px;
                display: flex;
                background: #FFFFFF;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                border-radius: 6px;
                img {
                    top: 0px;
                    position: static;
                    padding: 16px 10px 16px 16px;
                }
                .lines {
                    position: static;
                    display: flex;
                    flex-direction: column;
                    align-items: start;
                    justify-content: center;
                    .line {
                        height: 4px;
                        margin: 5px 0px;
                        background: #E4F3FF;
                        border-radius: 5px;
                        &:nth-child(1) {
                            width: 135px;
                        }
                        &:nth-child(2) {
                            width: 110px;
                        }
                        &:nth-child(3) {
                            width: 85px;
                        }
                    }
                }
            }
        }
        .block__text {
            padding-left: 120px;
            display: flex;
            align-items: start;
            flex-direction: column;
            p {
                font-size: 18px;
                text-align: left;
                width: 100%;
                padding-top: 20px;
                padding-bottom: 30px;
                line-height: 30px;
            }
            h2 {
                line-height: 50px;
                color: #3E3E3E;
                text-align: start;
            }
            .text__container {
                display: flex;
                align-items: start;
                flex-direction: column;
                padding-bottom: 24px;
                .strike {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding-bottom: 15px;
                    span {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 30px;
                        letter-spacing: 0.02em;
                        color: #3D3D3F;
                        padding-left: 18px;
                    }
                }
            }
            .btn {
                background: #40A3F8;
                border-radius: 5px;
                padding: 10px 18px;
                display: flex;
                margin-left: 0px;
                img {
                    padding-left: 10px;
                }
            }
        }
    }
}
.section:nth-child(3) {
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
        margin: 0 auto;
        padding-top: 24px;
        width: 510px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.02em;
        color: rgba(61, 61, 63, 0.7);
    }
    .background__right {
        width: 438px;
        height: 494px;
        position: absolute;
        z-index: 0;
        right: 30px;
        background: linear-gradient(90.26deg, rgba(170, 128, 255, 0.7) 0.21%, rgba(160, 113, 255, 0.455) 97.38%);
        filter: blur(444px);
        transform: rotate(90deg);
    }
    .background__left {
        position: absolute;
        width: 438px;
        height: 494px;
        left: 10px;
        z-index: 0;
        background: #99D4ED;
        filter: blur(444px);
        transform: rotate(90deg);
    }
    .container__card {
        .cards.active {
            display: block;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-top: 75px;
            .card {
                width: 350px;
                height: 320px;
                left: 135px;
                top: 2277px;
                background: linear-gradient(138.88deg, rgba(254, 254, 254, 0) -2.44%, #F9F9F9 -2.43%, rgba(254, 254, 254, 0.51) 98.26%);
                filter: drop-shadow(0px 4px 120px rgba(0, 0, 0, 0.05));
                border-radius: 10px;
                .card__section {
                    padding: 60px 30px 30px 50px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    h5 {
                        padding-top: 20px;
                    }
                    p {
                        width: 224px;
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 22px;
                        text-align: center;
                        color: #999999;
                    }
                    .line {
                        width: 350px;
                        height: 18px;
                        background: #40A3F8;
                        opacity: 0.5;
                        box-shadow: 0px 4px 120px rgba(0, 0, 0, 0.05);
                        border-radius: 0px 0px 10px 10px;
                        position: relative;
                        left: -10px;
                        bottom: -25px;
                    }
                }
            }
        }
        .cards {
            display: none;
        }
    }
    .arrow__dot {
        padding-top: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        .dot {
            width: 18px;
            height: 18px;      
            background: #DBE8EE;
            cursor: pointer;
            border-radius: 50%;
            margin: 0px 10px;
            opacity: 1;
            transition: all 0.7s ease;
        }
        .dot.active {
            width: 60px;
            height: 15px;
            background: #40A3F8;
            border-radius: 6px;
            cursor: pointer;
            border-radius: 8px;
            opacity: 1;
            transition: all 0.4s ease;
        }
    }
}
.section:nth-child(4) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .lione {
        padding: 16px 0px;
        margin: 0 auto;
        display: flex;
    }
    p  {
        margin: 0 auto;
        width: 530px;
        text-align: center;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
    }
    .container__card {
        padding-top: 75px;
        padding-bottom: 60px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .doctor__card {
            width: 270px;
            height: 400px;
            background: linear-gradient(138.88deg, rgba(254, 254, 254, 0) -2.44%, #F9F9F9 -2.43%, rgba(254, 254, 254, 0.51) 98.26%);
            border: 1px solid #EEEFF1;
            filter: drop-shadow(0px 4px 120px rgba(0, 0, 0, 0.05));
            border-radius: 10px;
            .card {
                padding: 0px 10px 60px 10px;
                position: relative;
                .doctor {
                    position: absolute;
                    top: -10px;
                }
                h5 {
                    padding-top: 18px;
                    position: absolute;
                    top: 260px;
                    left: 50px;
                }
                p {
                    position: absolute;
                    top: 310px;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 22px;
                    color: #999999;
                    width: 240px;
                }
                .icon {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 120px;
                    position: absolute;
                    top: 350px;
                    left: 80px
                }
            }
        }
    }
    .btn__card {
        margin: 0px 0px 0px 490px;   
    }
}
.section:nth-child(5) {
    .background {
        position: absolute;
        width: 862.36px;
        height: 972.61px;
        left: 960px;
        top: 100px;
        z-index: 0;
        background: linear-gradient(90.26deg, rgba(251, 224, 129, 0.7) 0.21%, rgba(113, 255, 221, 0.455) 97.38%);
        opacity: 0.3;
        filter: blur(697.344px);
        transform: rotate(90deg);
    }
    .app__container {
        display: flex;
        justify-content: end;
        align-items: center;
        padding-top: 120px;
        padding-bottom: 120px;
        .mockup {
            position: absolute;
            left: 160px;
        }
        .text__app {
            display: flex;
            flex-direction: column;
            align-items: start;
            justify-content: end;
            h2 {
                width: 530px;
                text-align: left;
                line-height: 60px;
            }
            p {
                padding: 30px 0px;
                width: 530px;
                text-align: left;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
            }
            .block__btn {
                display: flex;
                img {
                    padding: 0px 5px;
                }
            }
        }
    }
}
.section:nth-child(6) {
    .background {
        position: absolute;
        width: 539.56px;
        height: 608.55px;
        background: #1DA1F2;
        opacity: 0.5;
        filter: blur(697.344px);
        transform: rotate(90deg);
        z-index: -1;
        left: -100px;
    }
    .background-green {
        position: absolute;
        width: 539.56px;
        height: 608.55px;
        left: 690px;
        z-index: -1;
        background: rgba(54, 255, 186, 0.62);
        opacity: 0.5;
        filter: blur(697.344px);
        transform: rotate(90deg);
    }
    .slider {
        padding-top: 70px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        overflow: hidden;
        .container__slider {
            display: flex;
            justify-content: space-between;
            .card {
                border-radius: 5px;
                width: 370px;
                height: 347px;
                background: linear-gradient(132.1deg, rgba(255, 255, 255, 0.88) 0.65%, #FAFAFA 103.13%);
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                .block__card {
                    position: relative;
                    padding: 22px;
                    .img {
                        position: absolute;
                        left: 150px
                    }
                    .quote {
                        position: absolute;
                        top: -20px;
                        left: 280px;
                    }
                    .block__star {
                        position: absolute;
                        top: 90px;
                        left: 250px;
                    }
                    h5 {
                        position: absolute;
                        top: 80px;
                        left: 30px
                    }
                    span {
                        position: absolute;
                        top: 110px;
                        left: 30px;

                        font-size: 14px;
                        line-height: 25px;
                        background: linear-gradient(90.26deg, #47C2FF 0.21%, rgba(99, 203, 254, 0.65) 97.38%);
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        background-clip: text;
                    }
                    p {
                        position: absolute;
                        top: 160px;
                        left: 30px;
                        text-align: left;

                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 25px;
                        color: #454D5D;
                    }
                }
                &:nth-child(2n) {
                    margin: 0px 15px;
                }
            }
        }
    }
}
.section:nth-child(7) {
    .container__appoinment {
        width: 100%;
        height: 341px;
        background: #40A3F8;
        border-radius: 12px;
        display: flex;
        justify-content: space-between;
        .block__appoinment {
            display: flex;
            align-items: start;
            flex-direction: column;
            padding-left: 145px;
            padding-top: 85px;
            h2 {
                color: #FFFFFF;
                width: 450px;
                line-height: 50px;
                text-align: start;
                padding-bottom: 25px;
            }
            .btn {
                background: #FFFFFF;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                border-radius: 5px;
                color: #40A3F8;
                margin-left: 0px;
                transition: all 0.7s ease;
            }
            .btn:hover {
                transition: all 0.5s ease;
                color: #ffffff;
                background: #4a9eff;
                box-shadow: 3px 4px 22px rgba(255, 255, 255, 0.05);
            }
        }
        img {
            position: relative;
            width: 600px;
            height: 452px;
            top: -111px;
            right: -70px;
        }
    }
}

@import "./components/footer";
@import "breakpoints/breakpoint-main";
