@import "./../mixins/fonts";

.header {
    padding-top: 15px;
    background: #edf9ff; /* Old browsers */
    background: -moz-linear-gradient(left,  #edf9ff 0%, #c5dcec 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  #edf9ff 0%,#cbdeec 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  #edf9ff 0%,#cde2f1 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#edf9ff', endColorstr='#b3cde0',GradientType=1 ); /* IE6-9 */
    height: 880px;
}
.navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .h2 {
        font-family: 'Rubik';
        font-style: normal;
        font-weight: 500;
        font-size: 31.635px;
        line-height: 47px;
        letter-spacing: 1px;
        color: #40A3F8;
    }
    .block__navigation {
        display: flex;
        align-items: center;
        ul {
            display: flex;
            align-items: center;
            justify-content: space-between;
            li {
                padding: 0px 25px;
                a {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 24px;
                    color: #6E5F5F;
                    mix-blend-mode: normal;
                    opacity: 0.3;
                }
                a:hover {
                    transition: all 0.6s ease;
                    color: #020202;
                    opacity: 0.7;
                    font-size: 17px;
                }
            }
        }
    }
}
.header__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 240px;
    .container__title {
        display: flex;
        align-items: start;
        flex-direction: column;
        h1 {
            width: 532px;
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 700;
            font-size: 48px;
            line-height: 70px;
            color: #3E3E3E;
            span {
                color: #40A3F8;
            }
        }
        p {
            padding-top: 30px;
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 30px;
            color: rgba(117, 117, 117, 0.46);
            width: 460px;
        }
        .form {
            margin-top: 50px;
            width: 570px;
            height: 90px;
            background: #FFFFFF;
            box-shadow: 0px 4px 22px rgba(228, 245, 252, 0.3);
            border-radius: 8px;
            position: relative;
            .text {
                position: absolute;
                width: 220px;
                height: 60px;
                left: 15px;
                top: 15px;
                background: rgba(245, 246, 250, 0.52);
                border-radius: 8px;

                position: absolute;

                padding-left: 30px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 24px;
                color: rgba(0, 0, 0, 0.3);
            }
            .text:hover {
                transition: all 0.9s ease;
                background-color: #40A3F8;
                color: rgba(255, 255, 255, 0.8)
            }
            .form__select {
                position: relative;
                width: 205px;
                height: 60px;
                background: rgba(245, 246, 250, 0.52);
                border-radius: 8px;
                left: 250px;
                top: 15px;
                display: flex;
                align-items: center;
                padding-left: 28px;
                .select {
                    .select__header {
                        cursor: pointer;
                        display: flex;
                        padding-left: 15px;
                        display: flex;
                        align-items: center;
                        .select__current {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 24px;
                            color: #3D3D3F;
                            width: 110px;
                        }
                        .select__icon {
                            padding-left: 0px;
                        }
                    }
                    .select__body {
                        border-top: 0;
                        left: 0;
                        position: absolute;
                        right: 0;
                        top: 100%;
                        opacity: 0;
                        transition: all 0.5s ease;

                        display: none;
                        width: 205px;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        .select__item {
                            cursor: pointer;
                            font-family: "Poppins";
                            font-style: normal;
                            font-weight: 400;
                            font-size: 13px;
                            line-height: 24px;
                            color: #3D3D3F;
                        }
                    }
                    .select__item {
                        cursor: pointer;
                        font-size: 16px;
                        line-height: 24px;
                        padding: 8px;
                    }
                    
                    .select__item:hover {
                        background-color: #f2f2f2;
                    }
                }
                
                .select.is-active .select__body {
                    transition: all 0.8s ease;
                    opacity: 1;

                    display: block;
                    width: 205px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    margin-top: -14px;
                    border-radius: 0px 0px 8px 8px;
                    .select__item {
                        cursor: pointer;
                        font-family: "Poppins";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 13px;
                        line-height: 24px;
                    }
                    .select__item:hover {
                        transition: all 0.9s ease;
                        background-color: #40A3F8;
                        color: rgba(255, 255, 255, 0.8);
                        border-radius: 9px;
                    }
                }
        
            }
            .form__select:hover {
                transition: all 0.9s ease;
                background-color: #40A3F8;
                color: rgba(255, 255, 255, 0.8)
            }

            .button {
                position: absolute;
                width: 70px;
                height: 60px;
                background: #40A3F8;
                border-radius: 8px;
                right: 15px;
                top: 15px;
                img {
                    position: absolute;
                    top: 12px;
                    left: 17px;
                }
            }
        }
        
       
    }
    .container__img {
        .img__block {
            position: relative;
            img:nth-child(1) {
                position: absolute;
                top: -74px;
                right: 20px;
                z-index: 100;
            }
            .img-1 {
                position: absolute;
                top: -100px;
                right: -40px;
                z-index: 1;
            }
            .block__icons-one {
                position: relative;
                right: 580px;
                top: 20px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-two {
                position: relative;
                right: 600px;
                top: 210px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-three {
                position: relative;
                right: 150px;
                top: 260px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-four {
                position: relative;
                right: 60px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-five {
                position: absolute;
                width: 215px;
                height: 65px;
                background-color: #fff;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                z-index: 120;

                right: 380px;
                top: 400px;
                img {
                    top: 11px;
                    left: 22px;
                }
                .block {
                    display: flex;
                    align-items: start;
                    flex-direction: column;
                    position: absolute;
                    left: 80px;
                    top: 15px;
                    .block__star {
                        display: flex;
                        justify-content: space-between;
                        width: 100px;
                        .star {
                            position: static;
                        }
                    }
                    .line__one {
                        position: absolute;
                        top: 24px;
                        width: 120.96px;
                        height: 3.76px;
                        background: #E4F3FF;
                    }
                    .line__two {
                        position: absolute;
                        top: 33px;
                        width: 110.96px;
                        height: 3.76px;
                        background: #E4F3FF;
                    }
                }
            }
        }
    }
}