.container {
    width: 1170px;
    margin: 0 auto;
}

.btn {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 14.997px;
    line-height: 22px;
    padding: 12px 28px;
    background: #40A3F8;
    border-radius: 5px;
    color: #FFFFFF;
    margin-left: 20px;
}
.btn:hover {
    background: #257ecc;
    transition: all 0.8s ease;
}
h1 {
    font-family: 'Rubik';
     font-style: normal;
     font-weight: 900;
     font-size: 31.635px;
     line-height: 47px;   
     letter-spacing: 1px;
     color: #40A3F8;
}
.section {
    padding-top: 110px;
}
h2 {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 70px;
    color: #3E3E3E;
    text-align: center;
}
h5 {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    color: #3D3D3F;
}
p {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    color: rgba(0, 0, 0, 0.5);
}